* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 0;
}

body {
  font-family: "Quicksand", sans-serif;
  font-size: 17px;
}
.body-container {
  width: 100%;
  display: flex;
  flex-direction: column;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: transparent;
}

.logo-img {
  max-width: 6em;
  display: inline-block;
}

.siteName {
  font-size: 1.5em;
  font-weight: 300;
  letter-spacing: 0.8px;
}

.left-side {
  height: 100vh;
  background-color: black;
  color: whitesmoke;
  padding: 5%;
  position: relative;
}

.spacer {
  height: 200px;
}

.main-text-content {
  position: absolute;
  bottom: 5rem;
}

.main-text-content h1 {
  font-size: 3.7em;
  font-weight: 400;
  max-width: 325px;
  line-height: 1.1;
}

.main-text-content p {
  font-size: 1.35em;
  max-width: 600px;
}

.slogan span {
  color: rgb(14, 175, 167);
}

.acsent-line {
  width: 100px;
  border-top: 5px solid rgb(14, 175, 167);
  margin-top: 25px;
}

.tap-target {
  background-color: rgb(14, 175, 167);
}
.tap-target h5 {
  line-height: 1em;
  margin-top: 0.5em;
  margin-bottom: 0.3em;
}
.tap-target p {
  font-size: 1.1rem;
}

.lets-talk {
  margin-top: 1.5rem;
  padding: 5px 40px;
  border-radius: 25px;
  background-color: rgb(14, 175, 167);
  color: white;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.8px;
}

.lets-talk:hover {
  cursor: pointer;
}

.right-side {
  background-color: rgb(14, 175, 167);
  height: 500px;
}

/* h1 animation code */
.ml9 {
  position: relative;
  font-weight: 200;
  font-size: 4em;
}

.ml9 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.2em;
  padding-right: 0.05em;
  padding-bottom: 0.1em;
  overflow: hidden;
}

.ml9 .letter {
  transform-origin: 50% 100%;
  display: inline-block;
}
.ml12 {
  font-weight: 200;
  text-transform: uppercase;
}

.ml12 .letter {
  display: inline-block;
}
.ml13 {
  text-transform: uppercase;
  font-weight: 200;
}

.ml13 .letter {
  display: inline-block;
}

.ml10 {
  position: relative;
  font-weight: 200;
  bottom: -10px;
  text-transform: uppercase;
}

.ml10 .text-wrapper {
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.ml10 .letter {
  display: inline-block;
  transform-origin: 0 0;
}

.ml2 {
  font-weight: 200;
  text-transform: uppercase;
}

.ml2 .letter {
  display: inline-block;
}
